/* AntD */

html {
   --main-color: #252525;
   --main-color-light: #353535;
   --bg: linear-gradient(rgba(0, 0, 0, 0), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --bg-dark: linear-gradient(rgba(0, 0, 0, 0.25), var(--main-color)),
      url(/static/media/WarrenBuffett.6849661d.jpg) no-repeat center center;
   --main-font: "Assistant", sans-serif;
   /* 'EB Garamond', serif; */
   --text-font: "Roboto Slab", serif;
   --number-font: "Stardos Stencil", cursive;
   /* "Special Elite", cursive; */
   margin: 0;
   padding: 0;
   -webkit-touch-callout: none; /* iOS Safari */
   -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
   user-select: none;
   height: 100%;
   width: 100%;
   background-color: #252525;
   background-color: var(--main-color);
}

::-webkit-scrollbar {
   width: 8px;
}
::-webkit-scrollbar-track {
   background: #252525;
   background: var(--main-color);
}
::-webkit-scrollbar-thumb {
   background-color: #454545;
   border-radius: 20px;
}

* {
   box-sizing: border-box;
   margin: 0 0;
}

body {
   background-color: #252525 !important;
   background-color: var(--main-color) !important;
}

.App {
   width: 100%;
